import { useState, useCallback } from 'react';
import { Upload, FileText, X, Loader2 } from 'lucide-react';
import { extractTextFromPDF, createMockPDFData } from '../utils/pdf';
import { processChapterWithAI } from '../services/ai';
import { db } from '../db';
import type { Chapter, Flashcard, Book } from '../types';

interface PDFUploadProps {
  onUploadComplete: () => void;
}

export default function PDFUpload({ onUploadComplete }: PDFUploadProps) {
  const [isDragging, setIsDragging] = useState(false);
  const [isProcessing, setIsProcessing] = useState(false);
  const [progress, setProgress] = useState({ current: 0, total: 0, stage: '' });
  const [error, setError] = useState<string | null>(null);

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(true);
  }, []);

  const handleDragLeave = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
  }, []);

  const processFile = async (file: File) => {
    if (!file.type.includes('pdf') && !file.name.endsWith('.pdf')) {
      setError('Please upload a PDF file');
      return;
    }

    setIsProcessing(true);
    setError(null);

    try {
      // Extract text from PDF
      setProgress({ current: 0, total: 1, stage: 'Extracting text from PDF...' });
      const pdfData = await extractTextFromPDF(file);

      // Create book record
      const book: Book = {
        id: crypto.randomUUID(),
        title: file.name.replace('.pdf', ''),
        totalPages: pdfData.numPages,
        uploadedAt: new Date(),
        chapters: [],
      };

      await db.books.add(book);

      // Process chapters
      setProgress({ current: 0, total: pdfData.chapters.length, stage: 'Processing chapters with AI...' });

      for (let i = 0; i < pdfData.chapters.length; i++) {
        const pdfChapter = pdfData.chapters[i];
        setProgress({ current: i + 1, total: pdfData.chapters.length, stage: `Processing chapter ${i + 1}...` });

        const chapter: Chapter = {
          id: crypto.randomUUID(),
          bookId: book.id,
          number: pdfChapter.number,
          title: pdfChapter.title,
          content: pdfChapter.content,
          createdAt: new Date(),
          updatedAt: new Date(),
        };

        // Process with AI
        const aiResult = await processChapterWithAI(chapter);

        chapter.summary = aiResult.summary;
        chapter.keyTerms = aiResult.keyTerms;
        chapter.formulas = aiResult.formulas;
        chapter.facts = aiResult.facts;
        chapter.questions = aiResult.questions;

        await db.chapters.add(chapter);
        book.chapters.push(chapter.id);

        // Create flashcards from key terms
        for (const term of aiResult.keyTerms) {
          const flashcard: Flashcard = {
            id: crypto.randomUUID(),
            chapterId: chapter.id,
            front: term.term,
            back: term.definition,
            difficulty: 'medium',
            interval: 1,
            repetition: 0,
            easinessFactor: 2.5,
            nextReviewDate: new Date(),
          };
          await db.flashcards.add(flashcard);
        }

        // Create flashcards from facts
        for (const fact of aiResult.facts.slice(0, 3)) {
          const flashcard: Flashcard = {
            id: crypto.randomUUID(),
            chapterId: chapter.id,
            front: 'What is this important fact?',
            back: fact,
            difficulty: 'medium',
            interval: 1,
            repetition: 0,
            easinessFactor: 2.5,
            nextReviewDate: new Date(),
          };
          await db.flashcards.add(flashcard);
        }

        // Save questions
        for (const question of aiResult.questions) {
          await db.questions.add(question);
        }
      }

      // Update book with chapter IDs
      await db.books.update(book.id, { chapters: book.chapters });

      setIsProcessing(false);
      onUploadComplete();
    } catch (err) {
      setError('Failed to process PDF. Please try again.');
      setIsProcessing(false);
      console.error(err);
    }
  };

  const handleDrop = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);

    const files = e.dataTransfer.files;
    if (files.length > 0) {
      processFile(files[0]);
    }
  }, []);

  const handleFileInput = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      processFile(files[0]);
    }
  }, []);

  const loadSampleData = async () => {
    setIsProcessing(true);
    setError(null);

    try {
      const pdfData = createMockPDFData();

      const book: Book = {
        id: crypto.randomUUID(),
        title: 'Real Estate Principles - Sample',
        totalPages: pdfData.numPages,
        uploadedAt: new Date(),
        chapters: [],
      };

      await db.books.add(book);

      setProgress({ current: 0, total: pdfData.chapters.length, stage: 'Processing sample chapters...' });

      for (let i = 0; i < pdfData.chapters.length; i++) {
        const pdfChapter = pdfData.chapters[i];
        setProgress({ current: i + 1, total: pdfData.chapters.length, stage: `Processing chapter ${i + 1}...` });

        const chapter: Chapter = {
          id: crypto.randomUUID(),
          bookId: book.id,
          number: pdfChapter.number,
          title: pdfChapter.title,
          content: pdfChapter.content,
          createdAt: new Date(),
          updatedAt: new Date(),
        };

        const aiResult = await processChapterWithAI(chapter);

        chapter.summary = aiResult.summary;
        chapter.keyTerms = aiResult.keyTerms;
        chapter.formulas = aiResult.formulas;
        chapter.facts = aiResult.facts;
        chapter.questions = aiResult.questions;

        await db.chapters.add(chapter);
        book.chapters.push(chapter.id);

        for (const term of aiResult.keyTerms) {
          const flashcard: Flashcard = {
            id: crypto.randomUUID(),
            chapterId: chapter.id,
            front: term.term,
            back: term.definition,
            difficulty: 'medium',
            interval: 1,
            repetition: 0,
            easinessFactor: 2.5,
            nextReviewDate: new Date(),
          };
          await db.flashcards.add(flashcard);
        }

        for (const fact of aiResult.facts.slice(0, 3)) {
          const flashcard: Flashcard = {
            id: crypto.randomUUID(),
            chapterId: chapter.id,
            front: 'What is this important fact?',
            back: fact,
            difficulty: 'medium',
            interval: 1,
            repetition: 0,
            easinessFactor: 2.5,
            nextReviewDate: new Date(),
          };
          await db.flashcards.add(flashcard);
        }

        for (const question of aiResult.questions) {
          await db.questions.add(question);
        }
      }

      await db.books.update(book.id, { chapters: book.chapters });

      setIsProcessing(false);
      onUploadComplete();
    } catch (err) {
      setError('Failed to load sample data.');
      setIsProcessing(false);
      console.error(err);
    }
  };

  if (isProcessing) {
    return (
      <div className="max-w-md mx-auto text-center py-12">
        <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-primary-100 dark:bg-primary-900/30 flex items-center justify-center">
          <Loader2 className="w-8 h-8 text-primary-500 animate-spin" />
        </div>
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
          Processing your PDF
        </h3>
        <p className="text-gray-600 dark:text-gray-400 mb-4">{progress.stage}</p>
        <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
          <div
            className="bg-primary-500 h-2 rounded-full transition-all duration-300"
            style={{ width: `${(progress.current / progress.total) * 100}%` }}
          />
        </div>
        <p className="text-sm text-gray-500 dark:text-gray-400 mt-2">
          {progress.current} of {progress.total}
        </p>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="text-center mb-8">
        <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
          Upload Your Study Material
        </h2>
        <p className="text-gray-600 dark:text-gray-400">
          Drag and drop your PDF textbook to get started
        </p>
      </div>

      <div
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        className={`relative border-2 border-dashed rounded-xl p-8 text-center transition-colors ${
          isDragging
            ? 'border-primary-500 bg-primary-50 dark:bg-primary-900/20'
            : 'border-gray-300 dark:border-gray-600 hover:border-gray-400 dark:hover:border-gray-500'
        }`}
      >
        <input
          type="file"
          accept=".pdf"
          onChange={handleFileInput}
          className="absolute inset-0 w-full h-full opacity-0 cursor-pointer"
        />
        <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-gray-100 dark:bg-gray-700 flex items-center justify-center">
          <Upload className="w-8 h-8 text-gray-400" />
        </div>
        <p className="text-lg font-medium text-gray-900 dark:text-white mb-1">
          Drop your PDF here
        </p>
        <p className="text-sm text-gray-500 dark:text-gray-400">
          or click to browse
        </p>
        <p className="text-xs text-gray-400 dark:text-gray-500 mt-2">
          Supports PDF files up to 50MB
        </p>
      </div>

      {error && (
        <div className="flex items-center gap-2 p-4 bg-red-50 dark:bg-red-900/20 text-red-600 dark:text-red-400 rounded-lg">
          <X className="w-5 h-5" />
          <span>{error}</span>
        </div>
      )}

      <div className="text-center">
        <p className="text-sm text-gray-500 dark:text-gray-400 mb-4">Don't have a PDF? Try our sample data.</p>
        <button
          onClick={loadSampleData}
          className="inline-flex items-center gap-2 px-4 py-2 bg-gray-100 dark:bg-gray-700 text-gray-700 dark:text-gray-300 rounded-lg hover:bg-gray-200 dark:hover:bg-gray-600 transition-colors"
        >
          <FileText className="w-4 h-4" />
          Load Sample Book
        </button>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 mt-8">
        {[
          { icon: '', title: 'AI Summaries', desc: 'Executive summaries for each chapter' },
          { icon: '', title: 'Smart Flashcards', desc: 'Spaced repetition for better retention' },
          { icon: '', title: 'Practice Quizzes', desc: 'Test your knowledge with MCQs' },
        ].map((feature, i) => (
          <div key={i} className="p-4 bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700">
            <div className="text-2xl mb-2">{feature.icon}</div>
            <h3 className="font-medium text-gray-900 dark:text-white">{feature.title}</h3>
            <p className="text-sm text-gray-500 dark:text-gray-400">{feature.desc}</p>
          </div>
        ))}
      </div>
    </div>
  );
}