function getDefaultKeyTerms(): KeyTerm[] {
  return [
    { term: 'Real Estate', definition: 'Land and everything permanently attached to it.' },
    { term: 'Real Property', definition: 'Land plus all permanent attachments and rights.' },
    { term: 'Personal Property', definition: 'Movable items not permanently attached to land.' },
  ];
}

function getDefaultFacts(): string[] {
  return [
    'Real estate is immobile, indestructible, and unique.',
    'Location is the most important factor affecting value.',
    'Real estate markets are local in nature.',
  ];
}

// Local processing fallback (original functionality)
function processChapterLocally(chapter: Chapter): AIProcessingResult {
  const title = chapter.title.toLowerCase();

  if (title.includes('introduction') || title.includes('fundamentals')) {
    return generateIntroductionContent(chapter);
  } else if (title.includes('ownership') || title.includes('rights')) {
    return generateOwnershipContent(chapter);
  } else if (title.includes('finance') || title.includes('loan') || title.includes('mortgage')) {
    return generateFinanceContent(chapter);
  } else if (title.includes('contract') || title.includes('agency')) {
    return generateContractContent(chapter);
  }

  return generateGenericContent(chapter);
}

