import { useState } from 'react';
import { ChevronDown, ChevronUp, BookOpen, KeyRound, Calculator, Lightbulb, HelpCircle, CheckCircle } from 'lucide-react';
import type { Book, Chapter } from '../types';

interface ChapterListProps {
  book: Book;
  chapters: Chapter[];
  onChaptersUpdated: (bookId: string) => void;
}

export default function ChapterList({ book, chapters }: ChapterListProps) {
  const [expandedChapter, setExpandedChapter] = useState<string | null>(null);

  const toggleChapter = (chapterId: string) => {
    setExpandedChapter(expandedChapter === chapterId ? null : chapterId);
  };

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between mb-6">
        <div>
          <h2 className="text-xl font-bold text-gray-900 dark:text-white">{book.title}</h2>
          <p className="text-gray-600 dark:text-gray-400">
            {chapters.length} chapters  {book.totalPages} pages
          </p>
        </div>
      </div>

      <div className="space-y-3">
        {chapters.map((chapter) => (
          <div
            key={chapter.id}
            className="bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700 overflow-hidden"
          >
            <button
              onClick={() => toggleChapter(chapter.id)}
              className="w-full flex items-center justify-between p-4 hover:bg-gray-50 dark:hover:bg-gray-700/50 transition-colors"
            >
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 rounded-lg bg-primary-100 dark:bg-primary-900/30 flex items-center justify-center">
                  <BookOpen className="w-5 h-5 text-primary-600 dark:text-primary-400" />
                </div>
                <div className="text-left">
                  <p className="text-sm text-gray-500 dark:text-gray-400">
                    Chapter {chapter.number}
                  </p>
                  <h3 className="font-semibold text-gray-900 dark:text-white">
                    {chapter.title}
                  </h3>
                </div>
              </div>
              {expandedChapter === chapter.id ? (
                <ChevronUp className="w-5 h-5 text-gray-400" />
              ) : (
                <ChevronDown className="w-5 h-5 text-gray-400" />
              )}
            </button>

            {expandedChapter === chapter.id && (
              <div className="border-t border-gray-200 dark:border-gray-700 p-4 space-y-6">
                {/* Summary */}
                {chapter.summary && (
                  <div>
                    <h4 className="flex items-center gap-2 font-medium text-gray-900 dark:text-white mb-2">
                      <BookOpen className="w-4 h-4 text-primary-500" />
                      Executive Summary
                    </h4>
                    <p className="text-gray-600 dark:text-gray-400 text-sm leading-relaxed">
                      {chapter.summary}
                    </p>
                  </div>
                )}

                {/* Key Terms */}
                {chapter.keyTerms && chapter.keyTerms.length > 0 && (
                  <div>
                    <h4 className="flex items-center gap-2 font-medium text-gray-900 dark:text-white mb-3">
                      <KeyRound className="w-4 h-4 text-primary-500" />
                      Key Terms ({chapter.keyTerms.length})
                    </h4>
                    <div className="grid gap-2">
                      {chapter.keyTerms.map((term, i) => (
                        <div
                          key={i}
                          className="p-3 bg-gray-50 dark:bg-gray-700/50 rounded-lg"
                        >
                          <p className="font-medium text-gray-900 dark:text-white">
                            {term.term}
                          </p>
                          <p className="text-sm text-gray-600 dark:text-gray-400 mt-1">
                            {term.definition}
                          </p>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Formulas */}
                {chapter.formulas && chapter.formulas.length > 0 && (
                  <div>
                    <h4 className="flex items-center gap-2 font-medium text-gray-900 dark:text-white mb-3">
                      <Calculator className="w-4 h-4 text-primary-500" />
                      Important Formulas
                    </h4>
                    <div className="space-y-3">
                      {chapter.formulas.map((formula, i) => (
                        <div
                          key={i}
                          className="p-3 bg-primary-50 dark:bg-primary-900/20 rounded-lg border border-primary-100 dark:border-primary-800"
                        >
                          <p className="font-medium text-gray-900 dark:text-white">
                            {formula.name}
                          </p>
                          <div className="my-2 p-2 bg-white dark:bg-gray-800 rounded font-mono text-sm text-primary-600 dark:text-primary-400">
                            {formula.formula}
                          </div>
                          <p className="text-sm text-gray-600 dark:text-gray-400">
                            {formula.explanation}
                          </p>
                          {formula.example && (
                            <p className="text-sm text-gray-500 dark:text-gray-500 mt-1">
                              Example: {formula.example}
                            </p>
                          )}
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Must-Know Facts */}
                {chapter.facts && chapter.facts.length > 0 && (
                  <div>
                    <h4 className="flex items-center gap-2 font-medium text-gray-900 dark:text-white mb-3">
                      <Lightbulb className="w-4 h-4 text-primary-500" />
                      Must-Know Facts
                    </h4>
                    <div className="space-y-2">
                      {chapter.facts.map((fact, i) => (
                        <div
                          key={i}
                          className="flex items-start gap-2 p-2 bg-yellow-50 dark:bg-yellow-900/20 rounded-lg"
                        >
                          <CheckCircle className="w-4 h-4 text-yellow-600 dark:text-yellow-400 mt-0.5 flex-shrink-0" />
                          <p className="text-sm text-gray-700 dark:text-gray-300">{fact}</p>
                        </div>
                      ))}
                    </div>
                  </div>
                )}

                {/* Practice Questions */}
                {chapter.questions && chapter.questions.length > 0 && (
                  <div>
                    <h4 className="flex items-center gap-2 font-medium text-gray-900 dark:text-white mb-3">
                      <HelpCircle className="w-4 h-4 text-primary-500" />
                      Practice Questions ({chapter.questions.length})
                    </h4>
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                      Take the quiz to test your knowledge of this chapter.
                    </p>
                  </div>
                )}
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}