import { useState, useEffect } from 'react';
import { Play, CheckCircle, XCircle, Clock, Trophy, RotateCcw, ChevronRight } from 'lucide-react';
import { db } from '../db';
import type { Chapter, Question, QuizResult } from '../types';

interface QuizViewProps {
  chapters: Chapter[];
}

export default function QuizView({ chapters }: QuizViewProps) {
  const [selectedChapter, setSelectedChapter] = useState<string | 'all'>('all');
  const [questions, setQuestions] = useState<Question[]>([]);
  const [isQuizActive, setIsQuizActive] = useState(false);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null);
  const [answers, setAnswers] = useState<{ questionId: string; selectedAnswer: number; correct: boolean; timeSpent: number }[]>([]);
  const [showResult, setShowResult] = useState(false);
  const [startTime, setStartTime] = useState<number>(0);
  const [questionStartTime, setQuestionStartTime] = useState<number>(0);
  const [quizResults, setQuizResults] = useState<QuizResult[]>([]);

  useEffect(() => {
    loadQuizResults();
  }, []);

  const loadQuizResults = async () => {
    const results = await db.quizResults.orderBy('date').reverse().limit(10).toArray();
    setQuizResults(results);
  };

  const startQuiz = async () => {
    let quizQuestions: Question[] = [];

    if (selectedChapter === 'all') {
      // Get questions from all chapters
      const allQuestions = await db.questions.toArray();
      // Shuffle and take 10
      quizQuestions = allQuestions.sort(() => Math.random() - 0.5).slice(0, 10);
    } else {
      quizQuestions = await db.questions.where('chapterId').equals(selectedChapter).toArray();
    }

    if (quizQuestions.length === 0) {
      alert('No questions available for the selected chapter(s).');
      return;
    }

    setQuestions(quizQuestions);
    setIsQuizActive(true);
    setCurrentQuestionIndex(0);
    setSelectedAnswer(null);
    setAnswers([]);
    setShowResult(false);
    setStartTime(Date.now());
    setQuestionStartTime(Date.now());
  };

  const handleAnswerSelect = (index: number) => {
    if (selectedAnswer !== null) return; // Prevent changing answer
    setSelectedAnswer(index);
  };

  const handleNext = async () => {
    const currentQuestion = questions[currentQuestionIndex];
    const timeSpent = Math.floor((Date.now() - questionStartTime) / 1000);
    const isCorrect = selectedAnswer === currentQuestion.correctAnswer;

    const newAnswers = [
      ...answers,
      {
        questionId: currentQuestion.id,
        selectedAnswer: selectedAnswer!,
        correct: isCorrect,
        timeSpent,
      },
    ];
    setAnswers(newAnswers);

    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
      setSelectedAnswer(null);
      setQuestionStartTime(Date.now());
    } else {
      // Quiz complete
      const totalTime = Math.floor((Date.now() - startTime) / 1000);
      const correctCount = newAnswers.filter(a => a.correct).length;
      const score = Math.round((correctCount / questions.length) * 100);

      const result: QuizResult = {
        id: crypto.randomUUID(),
        date: new Date(),
        chapterId: selectedChapter === 'all' ? 'mixed' : selectedChapter,
        score,
        totalQuestions: questions.length,
        correctAnswers: correctCount,
        timeSpent: totalTime,
        answers: newAnswers,
      };

      await db.quizResults.add(result);
      setShowResult(true);
      loadQuizResults();
    }
  };

  const resetQuiz = () => {
    setIsQuizActive(false);
    setShowResult(false);
    setQuestions([]);
    setCurrentQuestionIndex(0);
    setSelectedAnswer(null);
    setAnswers([]);
  };

  if (showResult) {
    const correctCount = answers.filter(a => a.correct).length;
    const score = Math.round((correctCount / questions.length) * 100);

    return (
      <div className="max-w-lg mx-auto text-center">
        <div className="mb-6">
          <div className={`w-24 h-24 mx-auto rounded-full flex items-center justify-center mb-4 ${
            score >= 80 ? 'bg-green-100 dark:bg-green-900/30' :
            score >= 60 ? 'bg-yellow-100 dark:bg-yellow-900/30' :
            'bg-red-100 dark:bg-red-900/30'
          }`}>
            <Trophy className={`w-12 h-12 ${
              score >= 80 ? 'text-green-600 dark:text-green-400' :
              score >= 60 ? 'text-yellow-600 dark:text-yellow-400' :
              'text-red-600 dark:text-red-400'
            }`} />
          </div>
          <h2 className="text-3xl font-bold text-gray-900 dark:text-white mb-2">
            {score}%
          </h2>
          <p className="text-gray-600 dark:text-gray-400">
            You got {correctCount} out of {questions.length} correct
          </p>
        </div>

        <div className="grid grid-cols-2 gap-4 mb-6">
          <div className="p-4 bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700">
            <Clock className="w-5 h-5 text-gray-400 mx-auto mb-2" />
            <p className="text-2xl font-semibold text-gray-900 dark:text-white">
              {Math.floor(answers.reduce((a, b) => a + b.timeSpent, 0) / 60)}:
              {String(answers.reduce((a, b) => a + b.timeSpent, 0) % 60).padStart(2, '0')}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">Time</p>
          </div>
          <div className="p-4 bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700">
            <CheckCircle className="w-5 h-5 text-gray-400 mx-auto mb-2" />
            <p className="text-2xl font-semibold text-gray-900 dark:text-white">
              {correctCount}
            </p>
            <p className="text-sm text-gray-500 dark:text-gray-400">Correct</p>
          </div>
        </div>

        <div className="space-y-3 mb-6">
          {questions.map((q, i) => {
            const answer = answers[i];
            return (
              <div
                key={q.id}
                className={`p-3 rounded-lg flex items-center gap-3 ${
                  answer.correct
                    ? 'bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800'
                    : 'bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800'
                }`}
              >
                {answer.correct ? (
                  <CheckCircle className="w-5 h-5 text-green-600 dark:text-green-400 flex-shrink-0" />
                ) : (
                  <XCircle className="w-5 h-5 text-red-600 dark:text-red-400 flex-shrink-0" />
                )}
                <p className="text-sm text-gray-700 dark:text-gray-300 text-left truncate">
                  {q.question}
                </p>
              </div>
            );
          })}
        </div>

        <button
          onClick={resetQuiz}
          className="inline-flex items-center gap-2 px-6 py-3 bg-primary-500 text-white rounded-lg hover:bg-primary-600 transition-colors"
        >
          <RotateCcw className="w-4 h-4" />
          Take Another Quiz
        </button>
      </div>
    );
  }

  if (isQuizActive && questions.length > 0) {
    const currentQuestion = questions[currentQuestionIndex];

    return (
      <div className="max-w-2xl mx-auto">
        <div className="flex items-center justify-between mb-4">
          <p className="text-sm text-gray-600 dark:text-gray-400">
            Question {currentQuestionIndex + 1} of {questions.length}
          </p>
          <button
            onClick={resetQuiz}
            className="text-sm text-gray-500 hover:text-gray-700 dark:hover:text-gray-300"
          >
            Quit
          </button>
        </div>

        <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2 mb-6">
          <div
            className="bg-primary-500 h-2 rounded-full transition-all duration-300"
            style={{ width: `${((currentQuestionIndex + 1) / questions.length) * 100}%` }}
          />
        </div>

        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6 mb-6">
          <p className="text-lg font-medium text-gray-900 dark:text-white mb-6">
            {currentQuestion.question}
          </p>

          <div className="space-y-3">
            {currentQuestion.options.map((option, index) => (
              <button
                key={index}
                onClick={() => handleAnswerSelect(index)}
                disabled={selectedAnswer !== null}
                className={`w-full p-4 text-left rounded-lg border-2 transition-all ${
                  selectedAnswer === null
                    ? 'border-gray-200 dark:border-gray-700 hover:border-primary-300 dark:hover:border-primary-700'
                    : selectedAnswer === index
                    ? index === currentQuestion.correctAnswer
                      ? 'border-green-500 bg-green-50 dark:bg-green-900/20'
                      : 'border-red-500 bg-red-50 dark:bg-red-900/20'
                    : index === currentQuestion.correctAnswer
                    ? 'border-green-500 bg-green-50 dark:bg-green-900/20'
                    : 'border-gray-200 dark:border-gray-700 opacity-50'
                }`}
              >
                <div className="flex items-center gap-3">
                  <span className="w-8 h-8 rounded-full bg-gray-100 dark:bg-gray-700 flex items-center justify-center text-sm font-medium">
                    {String.fromCharCode(65 + index)}
                  </span>
                  <span className="text-gray-900 dark:text-white">{option}</span>
                </div>
              </button>
            ))}
          </div>

          {selectedAnswer !== null && (
            <div className={`mt-4 p-4 rounded-lg ${
              selectedAnswer === currentQuestion.correctAnswer
                ? 'bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800'
                : 'bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800'
            }`}>
              <p className={`font-medium ${
                selectedAnswer === currentQuestion.correctAnswer
                  ? 'text-green-800 dark:text-green-400'
                  : 'text-red-800 dark:text-red-400'
              }`}>
                {selectedAnswer === currentQuestion.correctAnswer ? 'Correct!' : 'Incorrect'}
              </p>
              <p className="text-sm text-gray-600 dark:text-gray-400 mt-1">
                {currentQuestion.explanation}
              </p>
            </div>
          )}
        </div>

        <button
          onClick={handleNext}
          disabled={selectedAnswer === null}
          className="w-full py-3 bg-primary-500 text-white rounded-lg hover:bg-primary-600 disabled:opacity-50 disabled:cursor-not-allowed transition-colors flex items-center justify-center gap-2"
        >
          {currentQuestionIndex < questions.length - 1 ? (
            <>
              Next Question
              <ChevronRight className="w-4 h-4" />
            </>
          ) : (
            'Finish Quiz'
          )}
        </button>
      </div>
    );
  }

  return (
    <div className="max-w-lg mx-auto">
      <div className="text-center mb-8">
        <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">Practice Quiz</h2>
        <p className="text-gray-600 dark:text-gray-400">
          Test your knowledge with multiple choice questions
        </p>
      </div>

      <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6 mb-6">
        <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
          Select Chapter
        </label>
        <select
          value={selectedChapter}
          onChange={(e) => setSelectedChapter(e.target.value)}
          className="w-full border border-gray-300 dark:border-gray-600 rounded-lg px-4 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-white"
        >
          <option value="all">All Chapters (Mixed)</option>
          {chapters.map((chapter) => (
            <option key={chapter.id} value={chapter.id}>
              Chapter {chapter.number}: {chapter.title}
            </option>
          ))}
        </select>
      </div>

      <button
        onClick={startQuiz}
        className="w-full py-4 bg-primary-500 text-white rounded-xl hover:bg-primary-600 transition-colors flex items-center justify-center gap-2 text-lg font-medium"
      >
        <Play className="w-5 h-5" />
        Start Quiz
      </button>

      {quizResults.length > 0 && (
        <div className="mt-8">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Recent Results</h3>
          <div className="space-y-2">
            {quizResults.map((result) => (
              <div
                key={result.id}
                className="flex items-center justify-between p-3 bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700"
              >
                <div>
                  <p className="text-sm text-gray-600 dark:text-gray-400">
                    {new Date(result.date).toLocaleDateString()}
                  </p>
                  <p className="font-medium text-gray-900 dark:text-white">
                    {result.correctAnswers}/{result.totalQuestions} correct
                  </p>
                </div>
                <div className={`text-lg font-bold ${
                  result.score >= 80 ? 'text-green-600 dark:text-green-400' :
                  result.score >= 60 ? 'text-yellow-600 dark:text-yellow-400' :
                  'text-red-600 dark:text-red-400'
                }`}>
                  {result.score}%
                </div>
              </div>
            ))}
          </div>
        </div>
      )}
    </div>
  );
}