import { useState, useEffect } from 'react';
import { TrendingUp, TrendingDown, Target, BookOpen, Brain, Award, AlertCircle } from 'lucide-react';
import { db, getWeakTopics, getProgressByBook } from '../db';
import type { QuizResult, Progress, Chapter } from '../types';

interface ProgressDashboardProps {
  bookId?: string;
}

export default function ProgressDashboard({ bookId }: ProgressDashboardProps) {
  const [quizResults, setQuizResults] = useState<QuizResult[]>([]);
  const [, setProgress] = useState<Progress | null>(null);
  const [weakTopics, setWeakTopics] = useState<string[]>([]);
  const [chapters, setChapters] = useState<Chapter[]>([]);
  const [stats, setStats] = useState({
    totalFlashcards: 0,
    dueFlashcards: 0,
    totalQuestions: 0,
    averageScore: 0,
    studyStreak: 0,
  });

  useEffect(() => {
    loadData();
  }, [bookId]);

  const loadData = async () => {
    // Load quiz results
    const results = await db.quizResults.orderBy('date').reverse().toArray();
    setQuizResults(results);

    // Load progress
    if (bookId) {
      const bookProgress = await getProgressByBook(bookId);
      if (bookProgress) {
        setProgress(bookProgress);
      }
      
      const topics = await getWeakTopics(bookId);
      setWeakTopics(topics);

      const bookChapters = await db.chapters.where('bookId').equals(bookId).toArray();
      setChapters(bookChapters);
    }

    // Load stats
    const allFlashcards = await db.flashcards.toArray();
    const now = new Date();
    const dueCards = allFlashcards.filter(f => new Date(f.nextReviewDate) <= now);
    const allQuestions = await db.questions.toArray();

    const avgScore = results.length > 0
      ? Math.round(results.reduce((sum, r) => sum + r.score, 0) / results.length)
      : 0;

    // Calculate study streak (simplified)
    const uniqueDates = [...new Set(results.map(r => new Date(r.date).toDateString()))];
    let streak = 0;
    const today = new Date().toDateString();
    const yesterday = new Date(Date.now() - 86400000).toDateString();
    
    if (uniqueDates.includes(today) || uniqueDates.includes(yesterday)) {
      streak = uniqueDates.length;
    }

    setStats({
      totalFlashcards: allFlashcards.length,
      dueFlashcards: dueCards.length,
      totalQuestions: allQuestions.length,
      averageScore: avgScore,
      studyStreak: streak,
    });
  };

  const recentResults = quizResults.slice(0, 5);

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-xl font-bold text-gray-900 dark:text-white">Progress Dashboard</h2>
          <p className="text-gray-600 dark:text-gray-400">Track your study progress and identify areas for improvement</p>
        </div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-2 sm:grid-cols-4 gap-4">
        {[
          { icon: Brain, label: 'Flashcards', value: stats.totalFlashcards, subtext: `${stats.dueFlashcards} due`, color: 'blue' },
          { icon: Target, label: 'Questions', value: stats.totalQuestions, subtext: 'available', color: 'green' },
          { icon: Award, label: 'Avg Score', value: `${stats.averageScore}%`, subtext: 'last 10 quizzes', color: 'yellow' },
          { icon: BookOpen, label: 'Streak', value: stats.studyStreak, subtext: 'days studied', color: 'purple' },
        ].map((stat, i) => (
          <div key={i} className="p-4 bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700">
            <div className={`w-10 h-10 rounded-lg bg-${stat.color}-100 dark:bg-${stat.color}-900/30 flex items-center justify-center mb-3`}>
              <stat.icon className={`w-5 h-5 text-${stat.color}-600 dark:text-${stat.color}-400`} />
            </div>
            <p className="text-2xl font-bold text-gray-900 dark:text-white">{stat.value}</p>
            <p className="text-sm text-gray-500 dark:text-gray-400">{stat.label}</p>
            <p className="text-xs text-gray-400 dark:text-gray-500 mt-1">{stat.subtext}</p>
          </div>
        ))}
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Weak Topics */}
        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
          <div className="flex items-center gap-2 mb-4">
            <AlertCircle className="w-5 h-5 text-red-500" />
            <h3 className="font-semibold text-gray-900 dark:text-white">Areas to Improve</h3>
          </div>

          {weakTopics.length === 0 ? (
            <div className="text-center py-8">
              <TrendingUp className="w-12 h-12 text-green-500 mx-auto mb-3" />
              <p className="text-gray-600 dark:text-gray-400">Great job! No weak topics identified.</p>
            </div>
          ) : (
            <div className="space-y-2">
              {weakTopics.map((topic, i) => (
                <div
                  key={i}
                  className="flex items-center gap-3 p-3 bg-red-50 dark:bg-red-900/20 rounded-lg"
                >
                  <TrendingDown className="w-4 h-4 text-red-500 flex-shrink-0" />
                  <p className="text-sm text-gray-700 dark:text-gray-300">{topic}</p>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Recent Quiz Results */}
        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
          <div className="flex items-center gap-2 mb-4">
            <Award className="w-5 h-5 text-primary-500" />
            <h3 className="font-semibold text-gray-900 dark:text-white">Recent Quiz Results</h3>
          </div>

          {recentResults.length === 0 ? (
            <div className="text-center py-8">
              <Target className="w-12 h-12 text-gray-400 mx-auto mb-3" />
              <p className="text-gray-600 dark:text-gray-400">No quizzes taken yet.</p>
            </div>
          ) : (
            <div className="space-y-3">
              {recentResults.map((result) => (
                <div
                  key={result.id}
                  className="flex items-center justify-between p-3 bg-gray-50 dark:bg-gray-700/50 rounded-lg"
                >
                  <div>
                    <p className="text-sm text-gray-500 dark:text-gray-400">
                      {new Date(result.date).toLocaleDateString()}
                    </p>
                    <p className="font-medium text-gray-900 dark:text-white">
                      {result.correctAnswers}/{result.totalQuestions} correct
                    </p>
                  </div>
                  <div className={`text-lg font-bold ${
                    result.score >= 80 ? 'text-green-600 dark:text-green-400' :
                    result.score >= 60 ? 'text-yellow-600 dark:text-yellow-400' :
                    'text-red-600 dark:text-red-400'
                  }`}>
                    {result.score}%
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      </div>

      {/* Chapter Progress */}
      {chapters.length > 0 && (
        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
          <div className="flex items-center gap-2 mb-4">
            <BookOpen className="w-5 h-5 text-primary-500" />
            <h3 className="font-semibold text-gray-900 dark:text-white">Chapter Progress</h3>
          </div>

          <div className="space-y-3">
            {chapters.map((chapter) => {
              const hasSummary = !!chapter.summary;
              const hasFlashcards = chapter.keyTerms && chapter.keyTerms.length > 0;
              const hasQuestions = chapter.questions && chapter.questions.length > 0;
              const progress = [hasSummary, hasFlashcards, hasQuestions].filter(Boolean).length;
              const progressPercent = (progress / 3) * 100;

              return (
                <div key={chapter.id} className="flex items-center gap-4">
                  <div className="flex-1">
                    <div className="flex items-center justify-between mb-1">
                      <p className="text-sm font-medium text-gray-900 dark:text-white">
                        Chapter {chapter.number}: {chapter.title}
                      </p>
                      <span className="text-xs text-gray-500 dark:text-gray-400">
                        {Math.round(progressPercent)}%
                      </span>
                    </div>
                    <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                      <div
                        className="bg-primary-500 h-2 rounded-full transition-all duration-300"
                        style={{ width: `${progressPercent}%` }}
                      />
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      )}
    </div>
  );
}