import { useState, useEffect } from 'react';
import { Key, Eye, EyeOff, Check, AlertCircle, Info, ExternalLink } from 'lucide-react';
import { hasApiKey, setApiKey, getApiKey } from '../services/ai';

interface APIKeySetupProps {
  onComplete: () => void;
  onSkip?: () => void;
}

export default function APIKeySetup({ onComplete, onSkip }: APIKeySetupProps) {
  const [apiKey, setApiKeyInput] = useState('');
  const [showKey, setShowKey] = useState(false);
  const [isValidating, setIsValidating] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);
  const [alreadyHasKey, setAlreadyHasKey] = useState(false);

  useEffect(() => {
    // Check if user already has a key stored
    if (hasApiKey()) {
      setAlreadyHasKey(true);
      const storedKey = getApiKey();
      if (storedKey) {
        setApiKeyInput(storedKey.substring(0, 8) + '');
      }
    }
  }, []);

  const validateAndSaveKey = async () => {
    if (!apiKey.trim() || apiKey.includes('')) {
      setError('Please enter a valid API key');
      return;
    }

    setIsValidating(true);
    setError(null);

    try {
      // Test the API key with a simple request
      const response = await fetch('https://api.tavily.com/search', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          api_key: apiKey.trim(),
          query: 'test',
          max_results: 1,
        }),
      });

      if (response.ok) {
        setApiKey(apiKey.trim());
        setSuccess(true);
        setTimeout(() => {
          onComplete();
        }, 1000);
      } else {
        const data = await response.json().catch(() => ({}));
        if (response.status === 401 || response.status === 403) {
          setError('Invalid API key. Please check your key and try again.');
        } else {
          setError(data.message || `Error validating key: ${response.status}`);
        }
      }
    } catch (err) {
      setError('Network error. Please check your connection and try again.');
    } finally {
      setIsValidating(false);
    }
  };

  const handleUpdateKey = () => {
    setAlreadyHasKey(false);
    setApiKeyInput('');
    setError(null);
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-gradient-to-br from-blue-50 to-indigo-100 dark:from-gray-900 dark:to-gray-800 p-4">
      <div className="w-full max-w-md bg-white dark:bg-gray-800 rounded-2xl shadow-xl overflow-hidden">
        {/* Header */}
        <div className="bg-gradient-to-r from-primary-500 to-primary-600 p-6 text-white">
          <div className="flex items-center gap-3">
            <div className="p-2 bg-white/20 rounded-lg">
              <Key className="w-6 h-6" />
            </div>
            <div>
              <h1 className="text-xl font-bold">API Key Setup</h1>
              <p className="text-sm text-white/80">Real Estate Study Assistant</p>
            </div>
          </div>
        </div>

        <div className="p-6 space-y-6">
          {success ? (
            <div className="text-center py-8">
              <div className="w-16 h-16 bg-green-100 dark:bg-green-900/30 rounded-full flex items-center justify-center mx-auto mb-4">
                <Check className="w-8 h-8 text-green-600 dark:text-green-400" />
              </div>
              <h2 className="text-xl font-semibold text-gray-900 dark:text-white mb-2">
                API Key Saved!
              </h2>
              <p className="text-gray-600 dark:text-gray-400">
                Your Tavily API key has been securely stored.
              </p>
            </div>
          ) : (
            <>
              {/* Info Box */}
              <div className="bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-lg p-4">
                <div className="flex items-start gap-3">
                  <Info className="w-5 h-5 text-blue-600 dark:text-blue-400 flex-shrink-0 mt-0.5" />
                  <div className="text-sm text-blue-800 dark:text-blue-200">
                    <p className="font-medium mb-1">Why do I need an API key?</p>
                    <p>
                      To use AI-powered features like Market Analysis and Comps Reports, 
                      you need a Tavily API key. Your key is stored locally in your browser 
                      and is never sent to our servers.
                    </p>
                  </div>
                </div>
              </div>

              {/* API Key Input */}
              <div className="space-y-2">
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">
                  Tavily API Key
                </label>
                <div className="relative">
                  <input
                    type={showKey ? 'text' : 'password'}
                    value={apiKey}
                    onChange={(e) => setApiKeyInput(e.target.value)}
                    placeholder={alreadyHasKey ? '' : 'tvly-xxxxxxxxxxxxxxxx'}
                    disabled={alreadyHasKey}
                    className="w-full px-4 py-3 pr-12 bg-gray-50 dark:bg-gray-700 border border-gray-200 dark:border-gray-600 rounded-lg text-gray-900 dark:text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-transparent disabled:opacity-60"
                  />
                  {!alreadyHasKey && (
                    <button
                      type="button"
                      onClick={() => setShowKey(!showKey)}
                      className="absolute right-3 top-1/2 -translate-y-1/2 text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
                    >
                      {showKey ? <EyeOff className="w-5 h-5" /> : <Eye className="w-5 h-5" />}
                    </button>
                  )}
                </div>
                {alreadyHasKey && (
                  <button
                    onClick={handleUpdateKey}
                    className="text-sm text-primary-600 dark:text-primary-400 hover:underline"
                  >
                    Update API key
                  </button>
                )}
              </div>

              {/* Get Key Link */}
              <div className="text-center">
                <a
                  href="https://tavily.com/#pricing"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="inline-flex items-center gap-1 text-sm text-primary-600 dark:text-primary-400 hover:underline"
                >
                  Don't have a Tavily API key? Get one free
                  <ExternalLink className="w-3 h-3" />
                </a>
              </div>

              {/* Error Message */}
              {error && (
                <div className="flex items-start gap-2 p-3 bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-lg text-red-700 dark:text-red-300 text-sm">
                  <AlertCircle className="w-4 h-4 flex-shrink-0 mt-0.5" />
                  <span>{error}</span>
                </div>
              )}

              {/* Action Buttons */}
              <div className="flex gap-3">
                {onSkip && !alreadyHasKey && (
                  <button
                    onClick={onSkip}
                    disabled={isValidating}
                    className="flex-1 px-4 py-3 border border-gray-300 dark:border-gray-600 text-gray-700 dark:text-gray-300 font-medium rounded-lg hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors disabled:opacity-50"
                  >
                    Skip for now
                  </button>
                )}
                {alreadyHasKey ? (
                  <button
                    onClick={onComplete}
                    className="flex-1 px-4 py-3 bg-primary-500 hover:bg-primary-600 text-white font-medium rounded-lg transition-colors"
                  >
                    Continue
                  </button>
                ) : (
                  <button
                    onClick={validateAndSaveKey}
                    disabled={isValidating || !apiKey.trim()}
                    className="flex-1 px-4 py-3 bg-primary-500 hover:bg-primary-600 disabled:bg-gray-300 dark:disabled:bg-gray-700 text-white font-medium rounded-lg transition-colors flex items-center justify-center gap-2"
                  >
                    {isValidating ? (
                      <>
                        <div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" />
                        Validating...
                      </>
                    ) : (
                      'Save API Key'
                    )}
                  </button>
                )}
              </div>

              {/* Privacy Note */}
              <p className="text-xs text-center text-gray-500 dark:text-gray-400">
                 Your API key is stored securely in your browser's localStorage and is never sent to our servers.
              </p>
            </>
          )}
        </div>
      </div>
    </div>
  );
}
