import { useState, useEffect } from 'react';
import { Calendar as CalendarIcon, CheckCircle, Clock, Target, Plus, Trash2 } from 'lucide-react';
import { db } from '../db';
import type { StudySchedule, DailyGoal, Chapter } from '../types';
import { format, addDays, startOfDay, isSameDay } from 'date-fns';

export default function StudyScheduleView() {
  const [schedule, setSchedule] = useState<StudySchedule | null>(null);
  const [chapters, setChapters] = useState<Chapter[]>([]);
  const [examDate, setExamDate] = useState('');
  const [isCreating, setIsCreating] = useState(false);

  useEffect(() => {
    loadSchedule();
    loadChapters();
  }, []);

  const loadSchedule = async () => {
    const existing = await db.studySchedules.toArray();
    if (existing.length > 0) {
      setSchedule(existing[0]);
    }
  };

  const loadChapters = async () => {
    const allChapters = await db.chapters.toArray();
    setChapters(allChapters);
  };

  const createSchedule = async () => {
    if (!examDate) return;

    const examDateObj = new Date(examDate);
    const today = startOfDay(new Date());
    const daysUntilExam = Math.ceil((examDateObj.getTime() - today.getTime()) / (1000 * 60 * 60 * 24));

    if (daysUntilExam <= 0) {
      alert('Exam date must be in the future');
      return;
    }

    // Delete existing schedule
    await db.studySchedules.clear();

    // Create daily goals
    const dailyGoals: DailyGoal[] = [];
    const allChapters = await db.chapters.toArray();
    const chapterIds = allChapters.map(c => c.id);
    
    // Distribute chapters across days
    const chaptersPerDay = Math.ceil(chapterIds.length / daysUntilExam);
    let chapterIndex = 0;

    for (let i = 0; i < daysUntilExam; i++) {
      const date = addDays(today, i);
      const dayChapters = chapterIds.slice(chapterIndex, chapterIndex + chaptersPerDay);
      chapterIndex += chaptersPerDay;

      dailyGoals.push({
        date,
        chapters: dayChapters,
        flashcards: Math.min(20, dayChapters.length * 5),
        quizQuestions: Math.min(10, dayChapters.length * 3),
        completed: false,
      });
    }

    const newSchedule: StudySchedule = {
      id: crypto.randomUUID(),
      examDate: examDateObj,
      dailyGoals,
      createdAt: new Date(),
    };

    await db.studySchedules.add(newSchedule);
    setSchedule(newSchedule);
    setIsCreating(false);
  };

  const toggleGoalComplete = async (index: number) => {
    if (!schedule) return;

    const updatedGoals = [...schedule.dailyGoals];
    updatedGoals[index].completed = !updatedGoals[index].completed;

    await db.studySchedules.update(schedule.id, { dailyGoals: updatedGoals });
    setSchedule({ ...schedule, dailyGoals: updatedGoals });
  };

  const deleteSchedule = async () => {
    if (!schedule) return;
    await db.studySchedules.delete(schedule.id);
    setSchedule(null);
  };

  const getChapterTitles = (chapterIds: string[]) => {
    return chapterIds
      .map(id => chapters.find(c => c.id === id)?.title)
      .filter(Boolean)
      .join(', ');
  };

  if (isCreating || !schedule) {
    return (
      <div className="max-w-lg mx-auto">
        <div className="text-center mb-8">
          <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-primary-100 dark:bg-primary-900/30 flex items-center justify-center">
            <CalendarIcon className="w-8 h-8 text-primary-600 dark:text-primary-400" />
          </div>
          <h2 className="text-2xl font-bold text-gray-900 dark:text-white mb-2">
            Create Study Schedule
          </h2>
          <p className="text-gray-600 dark:text-gray-400">
            Set your exam date and we'll create a personalized study plan
          </p>
        </div>

        <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
          <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            Exam Date
          </label>
          <input
            type="date"
            value={examDate}
            onChange={(e) => setExamDate(e.target.value)}
            min={format(addDays(new Date(), 1), 'yyyy-MM-dd')}
            className="w-full border border-gray-300 dark:border-gray-600 rounded-lg px-4 py-2 bg-white dark:bg-gray-700 text-gray-900 dark:text-white mb-4"
          />

          <button
            onClick={createSchedule}
            disabled={!examDate}
            className="w-full py-3 bg-primary-500 text-white rounded-lg hover:bg-primary-600 disabled:opacity-50 transition-colors flex items-center justify-center gap-2"
          >
            <Plus className="w-4 h-4" />
            Create Schedule
          </button>
        </div>

        <div className="mt-6 grid grid-cols-3 gap-4">
          {[
            { icon: '', label: 'Chapters', value: chapters.length },
            { icon: '', label: 'Days Left', value: examDate ? Math.ceil((new Date(examDate).getTime() - new Date().getTime()) / (1000 * 60 * 60 * 24)) : '-' },
            { icon: '', label: 'Daily Goal', value: examDate ? Math.ceil(chapters.length / Math.ceil((new Date(examDate).getTime() - new Date().getTime()) / (1000 * 60 * 60 * 24))) : '-' },
          ].map((stat, i) => (
            <div key={i} className="text-center p-4 bg-white dark:bg-gray-800 rounded-lg border border-gray-200 dark:border-gray-700">
              <div className="text-2xl mb-1">{stat.icon}</div>
              <p className="text-2xl font-bold text-gray-900 dark:text-white">{stat.value}</p>
              <p className="text-xs text-gray-500 dark:text-gray-400">{stat.label}</p>
            </div>
          ))}
        </div>
      </div>
    );
  }

  const today = startOfDay(new Date());
  const upcomingGoals = schedule.dailyGoals.filter(g => g.date >= today);
  const completedCount = schedule.dailyGoals.filter(g => g.completed).length;
  const progress = Math.round((completedCount / schedule.dailyGoals.length) * 100);

  return (
    <div className="max-w-2xl mx-auto">
      <div className="flex items-center justify-between mb-6">
        <div>
          <h2 className="text-xl font-bold text-gray-900 dark:text-white">Study Schedule</h2>
          <p className="text-gray-600 dark:text-gray-400">
            Exam: {format(new Date(schedule.examDate), 'MMMM d, yyyy')}
          </p>
        </div>
        <button
          onClick={deleteSchedule}
          className="p-2 text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20 rounded-lg transition-colors"
        >
          <Trash2 className="w-5 h-5" />
        </button>
      </div>

      <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-4 mb-6">
        <div className="flex items-center justify-between mb-2">
          <span className="text-sm font-medium text-gray-700 dark:text-gray-300">Progress</span>
          <span className="text-sm font-bold text-primary-600 dark:text-primary-400">{progress}%</span>
        </div>
        <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2">
          <div
            className="bg-primary-500 h-2 rounded-full transition-all duration-300"
            style={{ width: `${progress}%` }}
          />
        </div>
        <p className="text-sm text-gray-500 dark:text-gray-400 mt-2">
          {completedCount} of {schedule.dailyGoals.length} days completed
        </p>
      </div>

      <div className="space-y-3">
        <h3 className="font-semibold text-gray-900 dark:text-white">Upcoming Goals</h3>
        
        {upcomingGoals.length === 0 ? (
          <div className="text-center py-8 bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700">
            <Target className="w-12 h-12 text-gray-400 mx-auto mb-3" />
            <p className="text-gray-600 dark:text-gray-400">No more goals - you're ready for the exam!</p>
          </div>
        ) : (
          upcomingGoals.slice(0, 7).map((goal, index) => {
            const isToday = isSameDay(new Date(goal.date), today);
            const chapterTitles = getChapterTitles(goal.chapters);

            return (
              <div
                key={index}
                className={`p-4 rounded-xl border transition-colors ${
                  goal.completed
                    ? 'bg-green-50 dark:bg-green-900/20 border-green-200 dark:border-green-800'
                    : isToday
                    ? 'bg-primary-50 dark:bg-primary-900/20 border-primary-200 dark:border-primary-800'
                    : 'bg-white dark:bg-gray-800 border-gray-200 dark:border-gray-700'
                }`}
              >
                <div className="flex items-start gap-3">
                  <button
                    onClick={() => toggleGoalComplete(index)}
                    className={`mt-0.5 w-5 h-5 rounded border-2 flex items-center justify-center transition-colors ${
                      goal.completed
                        ? 'bg-green-500 border-green-500'
                        : 'border-gray-300 dark:border-gray-600 hover:border-primary-500'
                    }`}
                  >
                    {goal.completed && <CheckCircle className="w-3.5 h-3.5 text-white" />}
                  </button>

                  <div className="flex-1">
                    <div className="flex items-center gap-2 mb-1">
                      <p className={`font-medium ${
                        goal.completed
                          ? 'text-green-800 dark:text-green-400 line-through'
                          : 'text-gray-900 dark:text-white'
                      }`}>
                        {isToday ? 'Today' : format(new Date(goal.date), 'EEEE, MMM d')}
                      </p>
                      {isToday && !goal.completed && (
                        <span className="px-2 py-0.5 bg-primary-100 dark:bg-primary-900/30 text-primary-700 dark:text-primary-400 text-xs rounded-full">
                          Due
                        </span>
                      )}
                    </div>

                    {chapterTitles && (
                      <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                        {chapterTitles}
                      </p>
                    )}

                    <div className="flex items-center gap-4 text-xs text-gray-500 dark:text-gray-400">
                      <span className="flex items-center gap-1">
                        <Clock className="w-3 h-3" />
                        {goal.flashcards} flashcards
                      </span>
                      <span className="flex items-center gap-1">
                        <Target className="w-3 h-3" />
                        {goal.quizQuestions} questions
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            );
          })
        )}
      </div>
    </div>
  );
}