import { useState, useEffect } from 'react';
import { Brain, RotateCcw, ThumbsUp, ThumbsDown, MoreHorizontal, ChevronLeft, ChevronRight, CheckCircle } from 'lucide-react';
import { getDueFlashcards, updateFlashcardAfterReview } from '../db';
import type { Flashcard } from '../types';

export default function FlashcardView() {
  const [flashcards, setFlashcards] = useState<Flashcard[]>([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isFlipped, setIsFlipped] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [touchStart, setTouchStart] = useState<number | null>(null);
  const [touchEnd, setTouchEnd] = useState<number | null>(null);

  const minSwipeDistance = 50;

  useEffect(() => {
    loadFlashcards();
  }, []);

  const loadFlashcards = async () => {
    setIsLoading(true);
    const due = await getDueFlashcards();
    setFlashcards(due);
    setCurrentIndex(0);
    setIsFlipped(false);
    setIsLoading(false);
  };

  const currentCard = flashcards[currentIndex];

  const handleFlip = () => {
    setIsFlipped(!isFlipped);
  };

  const handleRate = async (quality: number) => {
    if (!currentCard) return;

    await updateFlashcardAfterReview(currentCard.id, quality);

    if (currentIndex < flashcards.length - 1) {
      setCurrentIndex(currentIndex + 1);
      setIsFlipped(false);
    } else {
      // All cards reviewed
      await loadFlashcards();
    }
  };

  const handlePrev = () => {
    if (currentIndex > 0) {
      setCurrentIndex(currentIndex - 1);
      setIsFlipped(false);
    }
  };

  const handleNext = () => {
    if (currentIndex < flashcards.length - 1) {
      setCurrentIndex(currentIndex + 1);
      setIsFlipped(false);
    }
  };

  // Touch handlers for swipe
  const onTouchStart = (e: React.TouchEvent) => {
    setTouchEnd(null);
    setTouchStart(e.targetTouches[0].clientX);
  };

  const onTouchMove = (e: React.TouchEvent) => {
    setTouchEnd(e.targetTouches[0].clientX);
  };

  const onTouchEnd = () => {
    if (!touchStart || !touchEnd) return;
    const distance = touchStart - touchEnd;
    const isLeftSwipe = distance > minSwipeDistance;
    const isRightSwipe = distance < -minSwipeDistance;

    if (isLeftSwipe) {
      handleRate(3); // Hard - swipe left
    } else if (isRightSwipe) {
      handleRate(5); // Easy - swipe right
    }
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-500" />
      </div>
    );
  }

  if (flashcards.length === 0) {
    return (
      <div className="text-center py-12">
        <div className="w-16 h-16 mx-auto mb-4 rounded-full bg-green-100 dark:bg-green-900/30 flex items-center justify-center">
          <Brain className="w-8 h-8 text-green-600 dark:text-green-400" />
        </div>
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-2">
          All Caught Up!
        </h3>
        <p className="text-gray-600 dark:text-gray-400 mb-4">
          You have no flashcards due for review.
        </p>
        <button
          onClick={loadFlashcards}
          className="inline-flex items-center gap-2 px-4 py-2 bg-primary-500 text-white rounded-lg hover:bg-primary-600 transition-colors"
        >
          <RotateCcw className="w-4 h-4" />
          Refresh
        </button>
      </div>
    );
  }

  return (
    <div className="max-w-lg mx-auto">
      <div className="flex items-center justify-between mb-6">
        <div>
          <h2 className="text-xl font-bold text-gray-900 dark:text-white">Flashcards</h2>
          <p className="text-gray-600 dark:text-gray-400">
            {currentIndex + 1} of {flashcards.length} due today
          </p>
        </div>
        <div className="flex items-center gap-2">
          <button
            onClick={handlePrev}
            disabled={currentIndex === 0}
            className="p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-700 disabled:opacity-50"
          >
            <ChevronLeft className="w-5 h-5" />
          </button>
          <button
            onClick={handleNext}
            disabled={currentIndex === flashcards.length - 1}
            className="p-2 rounded-lg hover:bg-gray-100 dark:hover:bg-gray-700 disabled:opacity-50"
          >
            <ChevronRight className="w-5 h-5" />
          </button>
        </div>
      </div>

      {/* Progress bar */}
      <div className="w-full bg-gray-200 dark:bg-gray-700 rounded-full h-2 mb-6">
        <div
          className="bg-primary-500 h-2 rounded-full transition-all duration-300"
          style={{ width: `${((currentIndex + 1) / flashcards.length) * 100}%` }}
        />
      </div>

      {/* Flashcard */}
      <div
        className="relative h-80 cursor-pointer perspective-1000"
        onClick={handleFlip}
        onTouchStart={onTouchStart}
        onTouchMove={onTouchMove}
        onTouchEnd={onTouchEnd}
      >
        <div
          className={`relative w-full h-full transition-transform duration-500 transform-style-3d ${
            isFlipped ? 'rotate-y-180' : ''
          }`}
          style={{
            transformStyle: 'preserve-3d',
            transform: isFlipped ? 'rotateY(180deg)' : 'rotateY(0deg)',
          }}
        >
          {/* Front */}
          <div
            className="absolute inset-0 backface-hidden bg-white dark:bg-gray-800 rounded-xl shadow-lg border border-gray-200 dark:border-gray-700 p-8 flex flex-col items-center justify-center text-center"
            style={{ backfaceVisibility: 'hidden' }}
          >
            <p className="text-sm text-gray-500 dark:text-gray-400 mb-4">Tap to flip</p>
            <p className="text-xl font-medium text-gray-900 dark:text-white">
              {currentCard.front}
            </p>
          </div>

          {/* Back */}
          <div
            className="absolute inset-0 backface-hidden bg-primary-50 dark:bg-primary-900/20 rounded-xl shadow-lg border border-primary-100 dark:border-primary-800 p-8 flex flex-col items-center justify-center text-center"
            style={{
              backfaceVisibility: 'hidden',
              transform: 'rotateY(180deg)',
            }}
          >
            <p className="text-sm text-primary-600 dark:text-primary-400 mb-4">Answer</p>
            <p className="text-xl font-medium text-gray-900 dark:text-white">
              {currentCard.back}
            </p>
          </div>
        </div>
      </div>

      {/* Rating buttons */}
      <div className="mt-6 grid grid-cols-4 gap-2">
        <button
          onClick={() => handleRate(1)}
          className="flex flex-col items-center gap-1 p-3 bg-red-100 dark:bg-red-900/30 text-red-700 dark:text-red-400 rounded-lg hover:bg-red-200 dark:hover:bg-red-900/50 transition-colors"
        >
          <ThumbsDown className="w-5 h-5" />
          <span className="text-xs">Again</span>
        </button>
        <button
          onClick={() => handleRate(3)}
          className="flex flex-col items-center gap-1 p-3 bg-orange-100 dark:bg-orange-900/30 text-orange-700 dark:text-orange-400 rounded-lg hover:bg-orange-200 dark:hover:bg-orange-900/50 transition-colors"
        >
          <MoreHorizontal className="w-5 h-5" />
          <span className="text-xs">Hard</span>
        </button>
        <button
          onClick={() => handleRate(4)}
          className="flex flex-col items-center gap-1 p-3 bg-blue-100 dark:bg-blue-900/30 text-blue-700 dark:text-blue-400 rounded-lg hover:bg-blue-200 dark:hover:bg-blue-900/50 transition-colors"
        >
          <ThumbsUp className="w-5 h-5" />
          <span className="text-xs">Good</span>
        </button>
        <button
          onClick={() => handleRate(5)}
          className="flex flex-col items-center gap-1 p-3 bg-green-100 dark:bg-green-900/30 text-green-700 dark:text-green-400 rounded-lg hover:bg-green-200 dark:hover:bg-green-900/50 transition-colors"
        >
          <CheckCircle className="w-5 h-5" />
          <span className="text-xs">Easy</span>
        </button>
      </div>

      <p className="text-center text-sm text-gray-500 dark:text-gray-400 mt-4">
        Swipe right for Easy, left for Hard  Tap card to flip
      </p>
    </div>
  );
}